.homes {
  display: grid;
  margin: 15rem 0;
  grid-column: center-start / center-end;

  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  grid-gap: 7rem;
}
.home {
  background-color: $color-grey-light-1;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 3.5rem;
  &__img {
    width: 100%;
    grid-row: 1 /2;
    grid-column: 1/ -1;
    z-index: 1;
  }
  &__like {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
    width: 2rem;
    height: 2rem;
    fill: $color-primary;
    z-index: 2;
    justify-self: end;
    margin: 1rem;
  }
  &__name {
    width: 80%;
    grid-row: 1 / 2;
    z-index: 1;
    grid-column: 1/ -1;
    justify-self: center;
    align-self: end;
    color: #fff;
    font-family: $font-display;
    font-size: 1.6rem;
    text-align: center;
    padding: 1.25rem;
    background-color: $color-secondary;
    font-weight: 400;
    transform: translateY(50%);
  }
  &__location,
  &__rooms {
    margin-top: 2.5rem;
  }
  &__location,
  &__rooms,
  &__area,
  &__price {
    font-size: 1.5rem;
    margin: 2rem;
    display: flex;
    align-items: center;
    svg {
      fill: $color-primary;
      height: 2rem;
      width: 2rem;
      margin-right: 1rem;
    }
  }
  &__btn {
    grid-column: 1 / -1;
  }
}
