.header {
  background-color: $color-grey-dark-2;
  background-image: linear-gradient(rgba($color-secondary, .93), rgba($color-secondary, .93)), url(../img/images/hero.jpeg);
  background-size: cover;
  background-position: center;
  grid-column: full-start / col-end 6;
  display: grid;
  grid-template-rows: 1fr min-content minmax(6rem, min-content) 1fr;
  grid-row-gap: 1.5rem;
  padding: 8rem;
  padding-top: 4rem;
  grid-template-columns: minmax(min-content, max-content);
  justify-content: center;
  @include df(800) {
    grid-column: 1/-1;
  }
  @include df(600) {
    padding: 5rem;
  }
  &__logo {
    height: 3rem;
    justify-self: center;
  }
  &__btn {
    align-self: start;
    justify-self: start;
  }
  &__seenon-text {
    display: grid;
    grid-template-columns: 1fr max-content 1fr;
    font-size: 1.6rem;
    color: $color-grey-light-2;
    grid-column-gap: 1.5rem;
    align-items: center;
    &:before, &:after {
      content: "";
      height: 1px;
      display: block;
      background-color: currentColor;
    }
  }
  &__seenon-logos {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 3rem;
    justify-items: center;
    align-items: center;
    img {
      filter: brightness(70%);
      max-height: 2.5rem;
      max-width: 100%;
    }
  }
}