/*
0 - 600px         mobile
600px - 900px     tablet portrait
900px - 1200px    tablet ladscape
1200px - 1800px   is where my normal styles apply
1800px +          large desctop
*/
/*
  Breackpoints argument choices
- phone
- tab-port
- tab-land
- lg-desktop

ORDER: base > typography > general > layout> page layout > components

1em = 16px
*/
* {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

a {
  display: inline-block;
  position: relative;
  color: inherit;
  text-decoration: none;
}

.container {
  display: grid;
  grid-template-rows: 80vh min-content 40vw repeat(3, min-content);
  grid-template-columns: [sidebar-start] 8rem [sidebar-end full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 14rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
}

@media only screen and (max-width: 1000px) {
  .container {
    grid-template-rows: 6rem 80vh min-content 40vw repeat(3, min-content);
    grid-template-columns: [full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 14rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
  }
}

@media only screen and (max-width: 800px) {
  .container {
    grid-template-rows: 6rem calc(100vh - 6rem);
  }
}

.heading-4 {
  font-family: "Josefin Sans", sans-serif;
}

@font-face {
  font-family: 'Nunito';
  src: url("../fonts/Nunito/");
  src: local("../fonts/Nunito/Nunito-Light"), url("../fonts/Nunito/Nunito-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Nunito';
  src: url("../fonts/Nunito/");
  src: local("../fonts/Nunito/Nunito-LightItalic"), url("../fonts/Nunito/Nunito-LightItalic.ttf") format("truetype");
  font-weight: 300;
  font-style: italic;
}

@font-face {
  font-family: 'Josefin Sans';
  src: url("../fonts/Josefin_Sans/");
  src: local("../fonts/Josefin_Sans/JosefinSans-LightItalic"), url("../fonts/Josefin_Sans/JosefinSans-LightItalic.ttf") format("truetype");
  font-weight: 300;
  font-style: italic;
}

@font-face {
  font-family: 'Josefin Sans';
  src: url("../fonts/Josefin_Sans/");
  src: local("../fonts/Josefin_Sans/JosefinSans-Italic"), url("../fonts/Josefin_Sans/JosefinSans-Italic.ttf") format("truetype");
  font-weight: 400;
  font-style: italic;
}

@font-face {
  font-family: 'Josefin Sans';
  src: url("../fonts/Josefin_Sans/");
  src: local("../fonts/Josefin_Sans/JosefinSans-Regular"), url("../fonts/Josefin_Sans/JosefinSans-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

html {
  font-size: 62.5%;
}

@media only screen and (max-width: 1200px) {
  html {
    font-size: 50%;
  }
}

body {
  font-family: "Nunito", sans-serif, serif;
  font-weight: 300;
  font-size: 1.6rem;
  line-height: 1.6;
  color: #6d5d4b;
}

.heading-1, .heading-2, .heading-3, .heading-4 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 400;
}

.heading-1 {
  font-size: 4.5rem;
  color: #f9f7f6;
  line-height: 1;
}

.heading-2 {
  font-size: 4rem;
  font-style: italic;
  line-height: 1;
}

.heading-2--light {
  color: #f9f7f6;
}

.heading-2--dark {
  color: #54483a;
}

.heading-3 {
  font-size: 1.6rem;
  color: #c69963;
  text-transform: uppercase;
}

.heading-4 {
  font-size: 1.9rem;
}

.heading-4--light {
  color: #f9f7f6;
}

.heading-4--dark {
  color: #54483a;
}

.btn {
  background-color: #c69963;
  color: #fff;
  border: none;
  border-radius: 0;
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.5rem;
  text-transform: uppercase;
  padding: 1.8rem 3rem;
  cursor: pointer;
  transition: all .3s;
}

.btn:hover {
  background-color: #b28451;
}

.mb-sm {
  margin-bottom: 2rem;
}

.mb-md {
  margin-bottom: 3rem;
}

.mb-lg {
  margin-bottom: 4rem;
}

.mb-hg {
  margin-bottom: 8rem;
}

.sidebar {
  background-color: #c69963;
  grid-column: sidebar-start / sidebar-end;
  grid-row: 1 / -1;
  display: flex;
  justify-content: center;
}

@media only screen and (max-width: 1100px) {
  .sidebar {
    grid-column: 1 / -1;
    grid-row: 1 / 2;
    justify-content: flex-end;
    align-items: center;
  }
}

.nav-btn {
  border: none;
  border-radius: 0;
  background-color: #fff;
  height: 2px;
  width: 4.4rem;
  margin-top: 4rem;
}

.nav-btn:before {
  content: "";
  background-color: #fff;
  height: 2px;
  width: 4.4rem;
  display: block;
}

.nav-btn:after {
  content: "";
  background-color: #fff;
  height: 2px;
  width: 4.4rem;
  display: block;
}

.nav-btn:before {
  transform: translateY(-1.5rem);
}

.nav-btn:after {
  transform: translateY(1.3rem);
}

@media only screen and (max-width: 1100px) {
  .nav-btn {
    margin-top: 0;
    margin-right: 3rem;
  }
  .nav-btn:before {
    transform: translateY(-1.2rem);
  }
  .nav-btn:after {
    transform: translateY(1rem);
  }
}

.realtors {
  background-color: #101d2c;
  grid-column: col-start 7 / full-end;
  padding: 3rem;
  display: grid;
  align-items: center;
  justify-items: center;
  grid-row-gap: 2rem;
  justify-items: center;
}

@media only screen and (max-width: 800px) {
  .realtors {
    grid-column: 1/-1;
  }
}

.realtors__list {
  display: grid;
  grid-template-columns: min-content max-content;
  grid-column-gap: 2rem;
  grid-row-gap: 5vh;
  align-items: center;
}

@media only screen and (max-width: 800px) {
  .realtors__list {
    grid-template-columns: repeat(3, min-content max-content);
  }
}

@media only screen and (max-width: 600px) {
  .realtors__list {
    grid-template-columns: min-content max-content;
  }
}

.realtors__img {
  width: 7rem;
  border-radius: 50%;
  display: block;
}

.realtors__sold {
  text-transform: uppercase;
  color: #aaa;
  margin-top: -3px;
}

.features {
  grid-column: center-start / center-end;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  grid-gap: 6rem;
  align-items: start;
  margin: 15rem 0;
}

.feature {
  display: grid;
  grid-template-columns: min-content 1fr;
  grid-row-gap: 1.5rem;
  grid-column-gap: 2.4rem;
}

.feature__icon {
  fill: #c69963;
  font-size: 4.5rem;
  width: 4.5rem;
  height: 4.5rem;
  grid-row: 1 / span 2;
  transform: translateY(-1rem);
}

.feature__text {
  font-size: 1.7rem;
}

.story__pictures {
  background-image: linear-gradient(rgba(198, 153, 99, 0.5), rgba(198, 153, 99, 0.5)), url(../img/images/back.jpg);
  grid-column: full-start / col-end 4;
  display: grid;
  grid-template-rows: repeat(6, 1fr);
  grid-template-columns: repeat(6, 1fr);
  align-items: center;
  background-size: cover;
}

@media only screen and (max-width: 800px) {
  .story__pictures {
    grid-column: 1/-1;
    padding: 6rem;
  }
}

.story__img--1 {
  width: 100%;
  grid-row: 2 / 6;
  grid-column: 2 / 6;
  box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.1);
}

@media only screen and (max-width: 800px) {
  .story__img--1 {
    grid-column: 1 / 5;
    grid-row: 1 / -1;
  }
}

.story__img--2 {
  width: 115%;
  grid-row: 4 / 5;
  grid-column: 4 / 7;
  z-index: 20;
  box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.1);
}

@media only screen and (max-width: 800px) {
  .story__img--2 {
    grid-row: 1 / -1;
    width: 100%;
  }
}

.story__content {
  background-color: #f9f7f6;
  grid-column: col-start 5 / full-end;
  padding: 6rem 8vw;
  display: grid;
  align-content: center;
  justify-items: start;
}

@media only screen and (max-width: 800px) {
  .story__content {
    grid-column: 1/-1;
    grid-row: 5 / 6;
  }
}

.story__text {
  font-size: 1.5rem;
  font-style: italic;
  margin-bottom: 4rem;
}

.gallery {
  background-color: #f9f7f6;
  grid-column: full-start / full-end;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(7, 5vw);
  grid-gap: 1.5rem;
  padding: 1.5rem;
}

.gallery__item--1 {
  grid-row: 1 / span 2;
  grid-column: 1 / span 2;
}

.gallery__item--2 {
  grid-row: 1 / span 3;
  grid-column: 3 / span 3;
}

.gallery__item--3 {
  grid-row: 1 / span 2;
  grid-column: 6 / 7;
}

.gallery__item--4 {
  grid-row: 1 / span 2;
  grid-column: 7 / -1;
}

.gallery__item--5 {
  grid-row: 3 / span 3;
  grid-column: 1 / span 2;
}

.gallery__item--6 {
  grid-row: 4 / span 2;
  grid-column: 3 / span 2;
}

.gallery__item--7 {
  grid-row: 4 / 5;
  grid-column: 5 / 6;
}

.gallery__item--8 {
  grid-row: 3 / span 2;
  grid-column: 6 / span 2;
}

.gallery__item--9 {
  grid-row: 3 / span 3;
  grid-column: 8 / -1;
}

.gallery__item--10 {
  grid-row: 6 / -1;
  grid-column: 1 / 2;
}

.gallery__item--11 {
  grid-row: 6 / -1;
  grid-column: 2 / span 2;
}

.gallery__item--12 {
  grid-row: 6 / -1;
  grid-column: 4 / 5;
}

.gallery__item--13 {
  grid-row: 5 / -1;
  grid-column: 5 / span 3;
}

.gallery__item--14 {
  grid-row: 6 / -1;
  grid-column: 8 / -1;
}

.gallery__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.homes {
  display: grid;
  margin: 15rem 0;
  grid-column: center-start / center-end;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  grid-gap: 7rem;
}

.home {
  background-color: #f9f7f6;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 3.5rem;
}

.home__img {
  width: 100%;
  grid-row: 1 /2;
  grid-column: 1/ -1;
  z-index: 1;
}

.home__like {
  grid-row: 1 / 2;
  grid-column: 2 / 3;
  width: 2rem;
  height: 2rem;
  fill: #c69963;
  z-index: 2;
  justify-self: end;
  margin: 1rem;
}

.home__name {
  width: 80%;
  grid-row: 1 / 2;
  z-index: 1;
  grid-column: 1/ -1;
  justify-self: center;
  align-self: end;
  color: #fff;
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.6rem;
  text-align: center;
  padding: 1.25rem;
  background-color: #101d2c;
  font-weight: 400;
  transform: translateY(50%);
}

.home__location, .home__rooms {
  margin-top: 2.5rem;
}

.home__location, .home__rooms, .home__area, .home__price {
  font-size: 1.5rem;
  margin: 2rem;
  display: flex;
  align-items: center;
}

.home__location svg, .home__rooms svg, .home__area svg, .home__price svg {
  fill: #c69963;
  height: 2rem;
  width: 2rem;
  margin-right: 1rem;
}

.home__btn {
  grid-column: 1 / -1;
}

.header {
  background-color: #6d5d4b;
  background-image: linear-gradient(rgba(16, 29, 44, 0.93), rgba(16, 29, 44, 0.93)), url(../img/images/hero.jpeg);
  background-size: cover;
  background-position: center;
  grid-column: full-start / col-end 6;
  display: grid;
  grid-template-rows: 1fr min-content minmax(6rem, min-content) 1fr;
  grid-row-gap: 1.5rem;
  padding: 8rem;
  padding-top: 4rem;
  grid-template-columns: minmax(min-content, max-content);
  justify-content: center;
}

@media only screen and (max-width: 800px) {
  .header {
    grid-column: 1/-1;
  }
}

@media only screen and (max-width: 600px) {
  .header {
    padding: 5rem;
  }
}

.header__logo {
  height: 3rem;
  justify-self: center;
}

.header__btn {
  align-self: start;
  justify-self: start;
}

.header__seenon-text {
  display: grid;
  grid-template-columns: 1fr max-content 1fr;
  font-size: 1.6rem;
  color: #aaa;
  grid-column-gap: 1.5rem;
  align-items: center;
}

.header__seenon-text:before, .header__seenon-text:after {
  content: "";
  height: 1px;
  display: block;
  background-color: currentColor;
}

.header__seenon-logos {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 3rem;
  justify-items: center;
  align-items: center;
}

.header__seenon-logos img {
  filter: brightness(70%);
  max-height: 2.5rem;
  max-width: 100%;
}

.footer {
  background-color: #101d2c;
  grid-column: full-start / full-end;
  padding: 8rem;
}

.nav {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  grid-gap: 2rem;
  align-items: center;
}

.nav__link:link, .nav__link:visited {
  font-size: 1.4rem;
  color: #fff;
  text-decoration: none;
  font-family: "Josefin Sans", sans-serif;
  text-transform: uppercase;
  text-align: center;
  padding: 1.5rem;
  display: block;
  transition: all .2s;
}

.nav__link:hover, .nav__link:active {
  background-color: rgba(255, 255, 255, 0.05);
  transform: translateY(0.4rem);
}

.copy {
  font-size: 1.4rem;
  color: #aaa;
  margin-top: 6rem;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
  width: 70%;
}
